<template>
    <v-container>
        <v-row class="mt-5" justify="center">
            <v-col cols="12">
                <v-simple-table v-if="plans">
                    <thead>
                    <tr>
                        <th class="text-left">Название</th>
                        <th class="text-left">ID</th>
                        <th class="text-left">Стоимость</th>
                        <th class="text-left">ID у провайдера</th>
                        <th></th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="plan in plans" :key="plan.id">
                        <td>{{plan.name}}</td>
                        <td>{{plan.id}}</td>
                        <td>{{plan.price}}</td>
                        <td>{{plan.provider_id}}</td>
                        <td><RouterLink :to="`/plans/${plan.id}`"><v-icon>mdi-account-edit</v-icon></RouterLink></td>
                    </tr>
                    </tbody>
                </v-simple-table>
                <v-btn to="/plans/add" class="primary mt-5">Добавить план</v-btn>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    export default {
        name: "Plans",
        data: () => ({
            plans: null
        }),
        methods: {
            async getPlans() {
                const res = await this.axios({url: `${process.env.VUE_APP_SERVER_URL}api/v1/plans/`, method: 'GET'})
                this.plans = res.data
            }
        },
        mounted() {
            this.getPlans()
        }
    }
</script>


<style scoped>
    a {
        text-decoration: none;
    }
    i {
        transition: all .3s;
        opacity: 0;
        cursor: pointer;
    }
    tr:hover i {
        opacity: 1;
    }
</style>